.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #e8505b;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

.header {
  padding-bottom: 30rem;
  background-image: url(../img/wave.svg);
  background-repeat: no-repeat;
  background-position: bottom;
}

.nav {
  display: flex;
  padding: 4rem 0;
  justify-content: center;
  flex-wrap: wrap;
  gap: 2rem;
}
@media (min-width: 768px) {
  .nav {
    justify-content: space-between;
    align-items: center;
  }
}
.nav__logo {
  width: 30rem;
}
.nav__links {
  display: flex;
  gap: 2rem;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
}
.nav__link {
  color: #000;
  font-weight: 800;
  font-size: 2rem;
}
.nav__link--red {
  color: #FFFFFF;
  background-color: #e8505b;
  padding: 1rem 2.5rem 1rem 2.5rem;
  border-radius: 1rem;
}

.pide {
  display: grid;
  gap: 4rem;
  grid-template-columns: 1fr;
  align-items: end;
}
@media (min-width: 768px) {
  .pide {
    grid-template-columns: repeat(2, 1fr);
  }
}
.pide__form {
  background-color: #e8505b;
  padding: 9rem 5rem;
  color: #FFFFFF;
}
.pide__title {
  margin-bottom: 9rem;
  text-align: center;
}
.pide__campo {
  display: flex;
  flex-direction: column;
  margin-bottom: 0.5rem;
}
.pide__campo label {
  text-transform: uppercase;
}
.pide__input {
  padding: 1.5rem;
  margin-top: 1rem;
}
.pide__cta {
  display: block;
  width: 100%;
  padding: 1.5rem;
  background-color: #455a64;
  border: none;
  border-radius: 1rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-weight: 700;
  font-size: 2rem;
}

.funcion {
  background-color: #e8505b;
}
.funcion__title {
  text-align: center;
  color: #FFFFFF;
}
.funcion__title span {
  display: block;
  font-size: 2rem;
}
.funcion__pasos {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  overflow: hidden;
}
@media (min-width: 768px) {
  .funcion__pasos {
    justify-content: space-between;
    flex-wrap: nowrap;
  }
}
.funcion__pasos div {
  max-width: 45rem;
}
.funcion__paso {
  transition: 0.2s ease;
}
.funcion__paso:hover {
  transform: scale(1.15) rotate(-5deg);
}

.testimonials__title {
  text-align: center;
  padding: 4rem;
}
.testimonials__grid {
  display: flex;
  gap: 2rem;
  flex-direction: column;
}
@media (min-width: 768px) {
  .testimonials__grid {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
  }
}

.testimonial {
  padding: 2rem;
  background-color: #455a64;
  border-radius: 1rem;
}
.testimonial p {
  color: #FFFFFF;
}
.testimonial__person {
  display: flex;
  gap: 2rem;
  align-items: center;
}
.testimonial__img {
  max-width: 10rem;
  width: 30%;
  border-radius: 50%;
}
.testimonial__name {
  margin: 0 0 0.5rem 0;
  font-weight: 700;
}
.testimonial__feedback {
  font-size: 2rem;
  line-height: 1.5;
  margin: 2rem 0 0 0;
  padding-left: 3rem;
  position: relative;
}
.testimonial__feedback::before {
  content: ' " ';
  font-size: 10rem;
  position: absolute;
  left: 0;
  top: -4rem;
  opacity: 0.2;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, sans-serif;
}
.testimonial:nth-child(1) {
  grid-column: 1/4;
}
.testimonial:nth-child(2) {
  grid-column: 4/5;
}
.testimonial:nth-child(3) {
  grid-column: 1/3;
}
.testimonial:nth-child(4) {
  grid-column: 3/5;
}
.testimonial:nth-child(5) {
  grid-column: 5/6;
  grid-row: 1/3;
}

.restaurants {
  background-color: #eceeef;
  margin: 4rem 0;
  padding-bottom: 4rem;
}
.restaurants__title {
  padding: 4rem;
  text-align: center;
  margin: 0;
  color: #455a64;
}

.restaurants__grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 4rem;
}
@media (min-width: 768px) {
  .restaurants__grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

.restaurant {
  display: grid;
  grid-template-columns: unset;
  gap: 4rem;
  justify-content: center;
  align-items: center;
  background-color: #FFFFFF;
  padding: 3rem;
  border-radius: 1rem;
  box-shadow: 2px 2px 2rem rgba(0, 0, 0, 0.152);
}
.restaurant img {
  margin: 0 auto;
}
.restaurant__title {
  margin: 0.5rem 0 0.8rem 0;
  font-size: 2.5rem;
  color: #e8505b;
  text-align: center;
}
.restaurant__paragraph {
  font-size: 1.7rem;
  line-height: 1.5;
}
@media (min-width: 768px) {
  .restaurant {
    grid-template-columns: 1fr 3fr;
  }
  .restaurant img {
    margin: unset;
  }
  .restaurant__title {
    text-align: start;
  }
}

.gana {
  padding-bottom: 10rem;
}
.gana__title {
  text-align: center;
  color: #455a64;
}
.gana__img {
  height: 100%;
  object-fit: cover;
}
.gana__content {
  display: grid;
  grid-template-columns: 1fr;
  gap: 5rem;
}
@media (min-width: 768px) {
  .gana__content {
    grid-template-columns: repeat(2, 1fr);
  }
}
.gana__texts {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.gana__texts p {
  color: #455a64;
  line-height: 1.5;
}
.gana__cta {
  background-color: #e8505b;
  color: #FFFFFF;
  padding: 1rem 3rem;
  border-radius: 1rem;
  width: max-content;
  font-size: 2rem;
  font-weight: 700;
}

.app {
  background-color: #eceeef;
  padding: 5rem 0;
}
.app__container {
  display: grid;
  grid-template-columns: 1fr;
  gap: 5rem;
  align-items: center;
}
@media (min-width: 768px) {
  .app__container {
    gap: 4rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.app__img {
  max-width: 30rem;
  margin: 0 auto;
  transition: 0.3s ease;
}
.app__img:hover {
  transform: scale(1.15) rotate(5deg);
}
.app__text {
  text-align: center;
  color: #455a64;
}
.app__text span {
  display: block;
  color: #000;
  font-size: 2rem;
  font-weight: 300;
  margin-top: 1rem;
}
.app__icons {
  display: flex;
  justify-content: space-around;
}
.app__icon {
  width: 45%;
  transition: 0.4s ease;
  cursor: pointer;
}
.app__icon:hover {
  transform: scale(1.25);
}

.footer {
  background-color: #455a64;
}
.footer__container {
  display: grid;
  grid-template-columns: 1fr;
  gap: 4rem;
  padding: 4rem 0;
}
@media (min-width: 768px) {
  .footer__container {
    grid-template-columns: repeat(3, 1fr);
  }
}
.footer__container p, .footer__container h3, .footer__container a {
  color: white;
  line-height: 1.8;
}
.footer__title {
  margin-bottom: 2rem;
  text-align: center;
}
@media (min-width: 768px) {
  .footer__title {
    text-align: start;
  }
}
.footer__links {
  display: grid;
  flex-direction: column;
  gap: 0.5rem;
  padding-left: unset;
  text-align: center;
}
@media (min-width: 768px) {
  .footer__links {
    text-align: start;
  }
}
.footer__li {
  list-style: none;
  font-size: 2rem;
}
.footer__paragraph {
  font-size: 2rem;
}

.copyright {
  background-color: #e8505b;
  padding: 2rem;
  text-align: center;
  color: #FFFFFF;
  margin: 0;
}